import { NeighbouringHill } from "./nearestHill";
import Modal from "react-modal";
import { useState } from "react";
import "./Button.css";

export const DisplayHillList = (props: { hills: Array<NeighbouringHill> }) => {
  return (
    <div
      style={{
        display: "flex",
        alignContent: "center",
        flexDirection: "column",
      }}
    >
      {props.hills.map((hill, idx) => (
        <DisplayHill hill={hill} key={idx} />
      ))}
    </div>
  );
};

export const DisplayHill = (props: { hill: NeighbouringHill }) => {
  const [modalIsOpen, setIsOpen] = useState(false);

  function openModal() {
    setIsOpen(true);
  }

  function afterOpenModal() {
    // references are now sync'd and can be accessed.
  }

  function closeModal() {
    setIsOpen(false);
  }

  return (
    <div>
      <button onClick={openModal} className="NeighbouringHill">
        {props.hill.hill.Name} {props.hill.distance.toFixed(0)} m
      </button>

      <Modal
        isOpen={modalIsOpen}
        onAfterOpen={afterOpenModal}
        onRequestClose={closeModal}
        className="Modal"
      >
        <div>
          <h1 className="Title">{props.hill.hill.Name}</h1>
          <div>
            <table className="Table">
              <tr>
                <td>Height</td>
                <td>{props.hill.hill.Metres} m</td>
              </tr>

              <tr>
                <td>Summit Feature</td>
                <td>{props.hill.hill.Feature}</td>
              </tr>

              <tr>
                <td>Observations</td>
                <td>{props.hill.hill.Observations}</td>
              </tr>

              <tr>
                <td>Prominence</td>
                <td>{props.hill.hill.Drop} m</td>
              </tr>

              <tr>
                <td>Classification Codes</td>
                <td>{props.hill.hill.Classification}</td>
              </tr>

              {/* <tr>
                <td>Coordinates</td>
                <td>
                  {props.hill.hill.Longitude.toFixed(5)}, {props.hill.hill.Latitude.toFixed(5)}
                </td>
              </tr> */}

              <tr>
                <td>Distance</td>
                <td>{props.hill.distance.toFixed(1)} m</td>
              </tr>

              <tr>
                <td>Bearing</td>
                <td>{props.hill.bearing.toFixed(0)}&deg;</td>
              </tr>

              <tr>
                <td>Direction</td>
                <td>{props.hill.cardinalDirection}</td>
              </tr>
            </table>
          </div>
          <button className="Button" onClick={closeModal}>
            close
          </button>
        </div>
      </Modal>
    </div>
  );
};
