import Papa from "papaparse";
import { Hill } from "./models";

export async function loadHillsDatabase(): Promise<[Hill]> {
  console.log("Loading hills db...")
  const data = Papa.parse(await loadHillsDatabaseCsvFile(), {
    header: true,
    dynamicTyping: false,
    skipEmptyLines: true,
  });
  return data.data;
}

async function loadHillsDatabaseCsvFile(): Promise<string> {
  const csvString = await fetch("./data/database.csv")
    .then((response) => response.text())
  return csvString;
}
    