import { useEffect, useState } from "react";

import haversineDistance from "haversine-distance";
import { DisplayHillList } from "./displayHill";
import { loadHillsDatabase } from "./hillsDatabase";
import { Coordinate, Hill } from "./models";
import Select from "react-select";
import { hillOptions } from "./models";

import "./Button.css";

const customStyles = {
  option: (provided: any) => ({
    ...provided,
    color: "black",
  }),
  control: (provided: any) => ({
    ...provided,
    color: "black",
    width: "100%",
  }),
  singleValue: (provided: any) => ({
    ...provided,
    color: "black",
  }),
  container: (provided: any) => ({
    ...provided,
    flex: 1,
  }),
  menu: (provided: any) => ({
    ...provided,
    flex: 1,
    width: "100%"
  }),
};

export const NearestHill = () => {
  const [hillsList, setHillsList] = useState<Hill[]>([]);
  const [nearestHills, setNearestHills] = useState<NeighbouringHill[]>();
  const [classification, setClassification] = useState<string>("Tu");
  const [loadingLocation, setLoadingLocation] = useState<boolean>(false);

  useEffect(() => {
    loadHillsDatabase().then((data) => {
      setHillsList(data);
    });
  }, []);

  const updateNearestHill = (coordinate: Coordinate) => {
    setNearestHills(
      getNearestNeighbours(coordinate, hillsList, classification)
    );
  };

  const updateDisplayData = () => {
    setLoadingLocation(true);
    const location = getLocation();
    location
      .then((result) => updateNearestHill(result))
      .then(() => setLoadingLocation(false))
      .then(() => console.log(NearestHill));
  };

  const handleChange = (selectedOption: any) => {
    console.log("Selection Change...");
    setClassification(selectedOption.value);
  };

  return (
    <div style={{display: "flex", alignContent: "justify", flexDirection: "column", justifyContent: "center"}}>
      <div style={{width: "100%"}}><Select
        className="Select"
        // menuPlacement="auto"
        // menuPosition="fixed"
        options={hillOptions}
        defaultValue={hillOptions[0]}
        onChange={handleChange}
        autoFocus={true}
        styles={customStyles}
        isSearchable={false}
      />
      </div>
      <button onClick={() => updateDisplayData()} className="Button">
        Find your nearest summit
      </button>
      <div>
      {loadingLocation? "Aquiring GPS location...": ""}
      {nearestHills && !loadingLocation? <DisplayHillList hills={nearestHills} /> : ""}
      </div>
    </div>
  );
};

export interface NeighbouringHill {
  hill: Hill;
  distance: number;
  bearing: number;
  cardinalDirection: string;
}

function getNearestNeighbours(
  coordinate: Coordinate,
  hills: Array<Hill>,
  classification: string,
  nNeighbours: number = 5
): Array<NeighbouringHill> {
  type SortPosition = {
    idx: number;
    distance: number;
  };

  const hillsOfClassification = hills.filter((hill) => {
    return Object(hill)[classification] === "1";
  });

  const distances: Array<SortPosition> = hillsOfClassification.map(
    (hill, index) => {
      const distance = haversineDistance(coordinate, {
        latitude: hill.Latitude,
        longitude: hill.Longitude,
      });
      return { idx: index, distance: distance };
    }
  );

  const sortedDistances = distances.sort(function (a, b) {
    return a.distance - b.distance;
  });

  const closestPositions = sortedDistances.slice(0, nNeighbours);
  const nearestHills = closestPositions.map(
    (position) => hillsOfClassification[position.idx]
  );

  let result = [];
  for (let i = 0; i < nearestHills.length; i++) {
    const bearing = getBearing(coordinate, {
      latitude: nearestHills[i].Latitude,
      longitude: nearestHills[i].Longitude,
    });
    const direction = getCardinalDirection(bearing);

    result.push({
      hill: nearestHills[i],
      distance: closestPositions[i].distance,
      bearing: bearing,
      cardinalDirection: direction,
    });
  }
  return result;
}

function getBearing(
  startPosition: Coordinate,
  endPosition: Coordinate
): number {
  const startLat = radians(startPosition.latitude);
  const startLong = radians(startPosition.longitude);
  const endLat = radians(endPosition.latitude);
  const endLong = radians(endPosition.longitude);

  var dLong = endLong - startLong;

  var dPhi = Math.log(
    Math.tan(endLat / 2.0 + Math.PI / 4.0) /
      Math.tan(startLat / 2.0 + Math.PI / 4.0)
  );
  if (Math.abs(dLong) > Math.PI) {
    if (dLong > 0.0) dLong = -(2.0 * Math.PI - dLong);
    else dLong = 2.0 * Math.PI + dLong;
  }

  return (degrees(Math.atan2(dLong, dPhi)) + 360.0) % 360.0;
}

function getCardinalDirection(bearing: number): string {
  if (bearing >= 0 && bearing < 22.5) {
    return "N";
  } else if (bearing === 22.5) {
    return "NNE";
  } else if (bearing > 22.5 && bearing < 67.5) {
    return "NE";
  } else if (bearing === 67.5) {
    return "ENE";
  } else if (bearing > 67.5 && bearing < 112.5) {
    return "E";
  } else if (bearing === 112.5) {
    return "ESE";
  } else if (bearing > 112.5 && bearing < 157.5) {
    return "SE";
  } else if (bearing === 157.5) {
    return "SSE";
  } else if (bearing > 157.5 && bearing < 202.5) {
    return "S";
  } else if (bearing === 202.5) {
    return "SSW";
  } else if (bearing > 202.5 && bearing < 247.5) {
    return "SW";
  } else if (bearing === 247.5) {
    return "WSW";
  } else if (bearing > 247.5 && bearing < 292.5) {
    return "W";
  } else if (bearing === 292.5) {
    return "WNW";
  } else if (bearing > 292.5 && bearing < 337.5) {
    return "NW";
  } else if (bearing === 337.5) {
    return "NNW";
  } else if (bearing > 337.5 && bearing < 360) {
    return "N";
  } else {
    return "undefined";
  }
}

function getLocation() {
  return new Promise<Coordinate>(function (resolve, reject) {
    navigator.geolocation.getCurrentPosition(
      function (pos) {
        const lat = pos.coords.latitude;
        const lon = pos.coords.longitude;
        resolve({ latitude: lat, longitude: lon });
      },
      () => console.log("Unable to get GPS"),
      { enableHighAccuracy: true }
    );
  });
}

function radians(n: number) {
  return n * (Math.PI / 180);
}
function degrees(n: number) {
  return n * (180 / Math.PI);
}
