export interface Coordinate {
  latitude: number;
  longitude: number;
}

export interface Hill {
  Name: string;
  Latitude: number;
  Longitude: number;
  Classification: string;
  Metres: number;
  Feature: string;
  Observations: string;
  Drop: number;
}

export const hillType = {
  Tump: "Tu",
  Munro: "M",
  Corbett: "C",
  Graham: "G",
  Donald: "D",
  "Munro Top": "MT",
  "Corbett Top": "CT",
  "Graham Top": "GT",
  "Donald Top": "DT",
  Marilyn: "Ma",
  Hump: "Hu",
  Simm: "Sim",
  Furth: "F",
  Wainwright: "W",
};

function populateOptions(): {}[] {
  let options: Array<{}> = [];
  for (const [name, code] of Object.entries(hillType)) {
    options.push({ value: code, label: name });
  }
  return options;
}

export const hillOptions = populateOptions();

